import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import PermissionButton from "../../plugin/components/PermissionButton";
import UserHandler from "../../script/handlers/UserHandler";
import InputArea from "../../plugin/components/InputArea";
import InfoTableItem from "../../plugin/components/InfoTableItem";
export default {
  name: "SystemJournal",
  extends: BaseIframe,
  components: {
    InfoTableItem,
    InputArea,
    InfoTable
  },
  data() {
    return {
      pagesize: 5,
      currentPage: 1,
      total: 0,
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      value2: "",
      requestWay: [
        { value: 1, label: "GET" },
        { value: 2, label: "POST" },
        { value: 3, label: "DELETE" },
        { value: 4, label: "PUT" }
      ],
      format: {
        man_id: "",
        req_way: "",
        router_name: ""
      },
      tableStyle: [
        { label: "ID", prop: "id", width: "" },
        { label: "操作者", prop: "user_id", width: "" },
        { label: "请求方法", prop: "req_method", width: "" },
        { label: "请求路由", prop: "req_path", width: "" },
        { label: "请求参数", prop: "req_params", width: "" },
        { label: "请求IP", prop: "req_ip", width: "" },
        { label: "请求时间", prop: "create_time", width: "" }
      ],
      tableData: [],
      records: [],
      pageInfo: new PageInfo(1, [5, 10, 15, 20],1),
      dialogAddVisible: false
    };
  },
  methods: {
    async getSystemOperationList() {
      const res = await this.$http.get("v1/backend/auth/operation-logs");
      console.log(res);
      if (res.data.code === 200) {
        this.tableData = res.data.data;
        this.total = res.data.data.total;
      }
      console.log(res);
    },
    search() {},
    // pageNumFn(val) {
    //   console.log(val, this.pageSize, this.pageNum);
    //   this.pageNum = val;
    // },
    // pageSizeFn(val) {
    //   console.log(val);
    //   this.pageSize = val;
    // },
    handleSizeChange(val) {
      this.pagesize = val
      this.getSystemOperationList()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getSystemOperationList()
    },
  },
  mounted() {
    this.getSystemOperationList();
  }
};



